            <el-table-column prop="number" label="订单编号" align="center"></el-table-column>
            <el-table-column prop="create_time" label="创建时间" align="center"></el-table-column>
            <el-table-column prop="state" label="状态" width="200" align="center">
              <template #default="o">
                <span v-if="o.row.state===1">待发货</span>
                <span v-else-if="o.row.state===2">配送中</span>
                <span v-else-if="o.row.state===3">已收货</span>
                <span v-else-if="o.row.state===4">已评价</span>
                <span v-else-if="o.row.state===5">退换货</span>
                <span v-else-if="o.row.state===6">已完成退货</span>
              </template>
            </el-table-column>
            <el-table-column
              prop="aid"
              label="地址信息"
              width="200"
              :show-overflow-tooltip="true"
              align="center"
            >
              <template #default="o">{{o.row.province}}{{o.row.city}}{{o.row.area}}{{o.row.address}}</template>
            </el-table-column>
            <el-table-column prop="total" label="总价格" align="center">
              <template slot-scope="{row}">{{ row.total | currency }}</template>
            </el-table-column>
            <el-table-column prop="backNote" label="备注" align="center" width="100">
              <template slot-scope="{row}">{{ row.backNote || '暂无数据' }}</template>
            </el-table-column>
            <el-table-column label="操作" align="center">
              <template #default="o">
                <el-button size="mini" @click="fetchOrderGood(o.row.id)">查看商品</el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-row type="flex" justify="center" style="margin-top:20px">
            <el-pagination
              @size-change="handleSizeChange1"
              @current-change="handleCurrentChange1"
              :current-page.sync="page1"
              :page-sizes="[5, 20, 30, 50]"
              :page-size="pageSize1"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total1"
            ></el-pagination>
          </el-row>
        </el-tab-pane>
        <el-tab-pane :label="'待发货('+total2+')'" name="2">
          <el-table :data="data2">
