            </el-table-column>
            <el-table-column label="操作" align="center">
              <template #default="o">
                <el-button size="mini" @click="fetchOrderGood(o.row.id)">查看商品</el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-row type="flex" justify="center" style="margin-top:20px">
            <el-pagination
              @size-change="fetchAllOrder3()"
              @current-change="fetchAllOrder3()"
              :current-page.sync="page3"
              :page-sizes="[5, 20, 30, 50]"
              :page-size="pageSize3"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total3"
            ></el-pagination>
          </el-row>
        </el-tab-pane>
        <el-tab-pane :label="'已收货('+total4+')'" name="4">
          <el-table :data="data4">
            <el-table-column prop="number" label="订单编号" align="center"></el-table-column>
            <el-table-column prop="create_time" label="创建时间" align="center"></el-table-column>
            <el-table-column
              prop="aid"
              label="地址信息"
              width="200"
              :show-overflow-tooltip="true"
              align="center"
            >
              <template #default="o">{{o.row.province}}{{o.row.city}}{{o.row.area}}{{o.row.address}}</template>
            </el-table-column>
            <el-table-column prop="total" label="总价格" align="center">
              <template slot-scope="{row}">{{ row.total | currency }}</template>
            </el-table-column>
            <el-table-column prop="backNote" label="备注" align="center" width="100">
              <template slot-scope="{row}">{{ row.backNote || '暂无数据' }}</template>
            </el-table-column>
            <el-table-column label="操作" align="center">
              <template #default="o">
                <el-button size="mini" @click="fetchOrderGood(o.row.id)">查看商品</el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-row type="flex" justify="center" style="margin-top:20px">
            <el-pagination
              @size-change="fetchAllOrder4()"
              @current-change="fetchAllOrder4()"
              :current-page.sync="page4"
              :page-sizes="[5, 20, 30, 50]"
              :page-size="pageSize4"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total4"
            ></el-pagination>
          </el-row>
        </el-tab-pane>
        <el-tab-pane :label="'已评价('+total5+')'" name="5">
          <el-table :data="data5">
            <el-table-column prop="number" label="订单编号" align="center"></el-table-column>
            <el-table-column prop="create_time" label="创建时间" align="center"></el-table-column>
            <el-table-column
              prop="aid"
              label="地址信息"
              width="200"
              :show-overflow-tooltip="true"
              align="center"
            >
              <template #default="o">{{o.row.province}}{{o.row.city}}{{o.row.area}}{{o.row.address}}</template>
            </el-table-column>
            <el-table-column prop="total" label="总价格" align="center">
              <template slot-scope="{row}">{{ row.total | currency }}</template>
            </el-table-column>
            <el-table-column prop="backNote" label="备注" align="center" width="100">
              <template slot-scope="{row}">{{ row.backNote || '暂无数据' }}</template>
            </el-table-column>
            <el-table-column label="操作" align="center">
              <template #default="o">
                <el-button size="mini" @click="fetchOrderGood(o.row.id)">查看商品</el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-row type="flex" justify="center" style="margin-top:20px">
            <el-pagination
              @size-change="fetchAllOrder5()"
              @current-change="fetchAllOrder5()"
              :current-page.sync="page5"
              :page-sizes="[5, 20, 30, 50]"
              :page-size="pageSize5"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total5"
            ></el-pagination>
          </el-row>
        </el-tab-pane>
        <el-tab-pane :label="'退换货('+total6+')'" name="6">
          <el-table :data="data6">
            <el-table-column prop="number" label="订单编号" align="center"></el-table-column>
            <el-table-column prop="create_time" label="创建时间" align="center"></el-table-column>
            <el-table-column
              prop="aid"
              label="地址信息"
              width="200"
              :show-overflow-tooltip="true"
              align="center"
            >
              <template #default="o">{{o.row.province}}{{o.row.city}}{{o.row.area}}{{o.row.address}}</template>
            </el-table-column>
            <el-table-column prop="total" label="总价格" align="center">
              <template slot-scope="{row}">{{ row.total | currency }}</template>
            </el-table-column>
            <el-table-column prop="backNote" label="备注" align="center" width="100">
              <template slot-scope="{row}">{{ row.backNote || '暂无数据' }}</template>
            </el-table-column>
            <el-table-column label="操作" align="center">
              <template #default="o">
                <el-button size="mini" @click="fetchOrderGood(o.row.id)">查看商品</el-button>
                <el-button size="mini" @click="handleReject(o.row.id)">完成退货</el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-row type="flex" justify="center" style="margin-top:20px">
            <el-pagination
              @size-change="fetchAllOrder6()"
              @current-change="fetchAllOrder6()"
              :current-page.sync="page6"
              :page-sizes="[5, 20, 30, 50]"
              :page-size="pageSize6"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total6"
            ></el-pagination>
          </el-row>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <el-dialog title="查看当前订单商品" :visible.sync="dialogVisible" width="70%">
      <el-table :data="GoodData">
        <el-table-column label="当前订单" prop="oid"></el-table-column>
        <el-table-column label="商品id" prop="gid"></el-table-column>
        <el-table-column label="商品名称" prop="name"></el-table-column>
        <el-table-column label="商品图片">
          <template #default="o">
            <img :src="o.row.thumb" alt width="100" height="100" />
          </template>
        </el-table-column>
        <el-table-column label="商品价格" prop="price"></el-table-column>
        <el-table-column label="购买数量" prop="count"></el-table-column>
        <el-table-column label="规格id" prop="color_id"></el-table-column>
        <el-table-column label="规格颜色" prop="color_name"></el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible=false">取消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "orders",
  data() {
    return {
      activeName: "1",
      GoodData: [],
      url: "/order",
      url1: "/ordergoods",
      data1: [],
      page1: 1,
      pageSize1: 5,
      total1: 0,
      oid: 0,

      data2: [],
      page2: 1,
      pageSize2: 5,
      total2: 0,

      data3: [],
      page3: 1,
      pageSize3: 5,
      total3: 0,

      data4: [],
      page4: 1,
      pageSize4: 5,
      total4: 0,

      data5: [],
      page5: 1,
      pageSize5: 5,
      total5: 0,

      data6: [],
      page6: 1,
      pageSize6: 5,
      total6: 0,
      // 弹框默认是关闭
      dialogVisible: false
    };
  },
  components: {},
  created() {},
  mounted() {
    this.fetchAllOrder1();
    this.fetchAllOrder2();
    this.fetchAllOrder3();
    this.fetchAllOrder4();
    this.fetchAllOrder5();
    this.fetchAllOrder6();
  },
  methods: {
    /**  handleTabClick(){
      if (this.activeName==="2"&&this.data2.length==0){
        this.fetchAllOrder2();
      }else if(this.activeName==="3"&&this.data3.length ==0){
        this.fetchAllOrder3();
      }else if(this.activeName==="4"&&this.data3.length ==0){
        this.fetchAllOrder4();
      }else if(this.activeName==="5"&&this.data3.length ==0){
        this.fetchAllOrder5();
      }else if(this.activeName==="6"&&this.data3.length ==0){
        this.fetchAllOrder6();
      }
    },  */
    // 修改发货状态
    handleSend(id){
      this.$http.put(this.url+'/'+id,{state:2}).then(res=>{
        if (res.data.code==200){
          this.fetchAllOrder1();
        this.fetchAllOrder2();
        this.fetchAllOrder3();
        this.$message.success("更新成功")
        }
      })
    },
    // 完成退货状态
    handleReject(id){
      this.$http.put(this.url+'/'+id,{state:6}).then(res=>{
        if (res.data.code==200){
        this.fetchAllOrder1();
        this.fetchAllOrder6();
        this.$message.success("退货成功")
        }
      })
    },
    handleSizeChange1() {
      this.fetchAllOrder1();
    },
    handleCurrentChange1() {
      this.fetchAllOrder1();
    },
    // 获取订单数据
    fetchAllOrder1() {
      this.$http
        .get(this.url, {
          params: {
            page: this.page1,
            pageSize: this.pageSize1
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            this.data1 = res.data.data;
            this.total1 = res.data.total;
            console.log(this.data1);
          }
        });
    },
    // 获取订单数据
    fetchAllOrder2() {
      this.$http
        .get(this.url, {
          params: {
            page: this.page2,
            pageSize: this.pageSize2,
            state: 1
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            this.data2 = res.data.data;
            this.total2 = res.data.total;
            console.log(this.data1);
          }
        });
    },
    // 获取订单数据
    fetchAllOrder3() {
      this.$http
        .get(this.url, {
          params: {
            page: this.page3,
            pageSize: this.pageSize3,
            state: 2
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            this.data3 = res.data.data;
            this.total3 = res.data.total;
            console.log(this.data3);
          }
        });
    },
    // 获取订单数据
    fetchAllOrder4() {
      this.$http
        .get(this.url, {
          params: {
            page: this.page4,
            pageSize: this.pageSize4,
            state: 3
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            this.data4 = res.data.data;
            this.total4 = res.data.total;
            console.log(this.data4);
          }
        });
    },
    // 获取订单数据
    fetchAllOrder5() {
      this.$http
        .get(this.url, {
          params: {
            page: this.page5,
            pageSize: this.pageSize5,
            state: 4
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            this.data5 = res.data.data;
            this.total5 = res.data.total;
            console.log(this.data5);
          }
        });
    },
    // 获取订单数据
    fetchAllOrder6() {
      this.$http
        .get(this.url, {
          params: {
            page: this.page6,
            pageSize: this.pageSize6,
            state: 5
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            this.data6 = res.data.data;
            this.total6 = res.data.total;
            console.log(this.data6);
          }
        });
    },
    // 获取订单商品数据
    fetchOrderGood(id) {
      (this.dialogVisible = true),
        this.$http
          .get(this.url1, {
            params: {
              oid: id,
              page: 1,
              pageSize: 1000
            }
          })
          .then(res => {
            if (res.data.code == 200) {
              this.GoodData = res.data.data;
              console.log(res);
            }
          });
    }
  },
  watch: {
    // 监测页的变化
    page(newValue, oldValue) {
      this.fetchData();
    },
    // 监测条数的变化
    pageSize(newValue, oldValue) {
      this.fetchData();
    }
  }
};
</script>
<style lang='scss' scoped>
.el-breadcrumb {
  line-height: 60px;
}
.el-card {
  margin-top: 20px;
  margin-left: 20px;
  // height: 450px;
}
</style>
